//
// Colors
// --------------------------------------------------
// Via Benefits Primary Colors
// --------------------------------------------------

.wtwBlue {
  background-color: @blue;
}

.wtwGreen {
  background-color: @green;
}

.wtwMagenta {
  background-color: @magenta;
}


// --------------------------------------------------
// VB Secondary Colors
// --------------------------------------------------
.wtwGray {
  background-color: @wtwGray;
}

.wtwViolet {
  background-color: #702082;
}


// --------------------------------------------------
// Semantic
// --------------------------------------------------
.brand-success {
  background-color: @brand-success;
}

.brand-info {
  background-color: @brand-info;
}

.brand-warning {
  background-color: @brand-warning;
}

.brand-danger {
  background-color: @brand-danger;
}


// --------------------------------------------------
// Other (Functional, etc.)
// --------------------------------------------------
.bg-software-blue {
  background-color: @software-blue;
}

// --------------------------------------------------
// Gray Scale
// --------------------------------------------------

.bg-white {
  background-color: @white;
  border: 1px solid @black;
}

.bg-software-gray-1 {
  background-color: @software-gray-1;
  border: 1px solid @software-gray-9;
}

.bg-software-gray-2 {
  background-color: @software-gray-2;
  border: 1px solid @software-gray-8;
}

.bg-software-gray-3 {
  background-color: @software-gray-3;
  border: 1px solid @software-gray-7;
}

.bg-software-gray-4 {
  background-color: @software-gray-4;
  border: 1px solid @software-gray-6;
}

.bg-software-gray-5 {
  background-color: @software-gray-5;
  border: 1px solid @software-gray-5;
}

.bg-software-gray-6 {
  background-color: @software-gray-6;
  border: 1px solid @software-gray-4;
}

.bg-software-gray-7 {
  background-color: @software-gray-7;
  border: 1px solid @software-gray-3;
}

.bg-software-gray-8 {
  background-color: @software-gray-8;
  border: 1px solid @software-gray-2;
}

.bg-software-gray-9 {
  background-color: #444;
  border: 1px solid @software-gray-1;
}

.bg-black {
  background-color: @black;
  border: 1px solid @white;
}


