//
// Conventional Appointment Scheduling Widget
//
//================================================

form.appt-calendar-wrapper {
	.clearfix;
	margin: 0 auto;
	width: 450px;
	
	> .appt-calendar {
		float: left;
		width: 65%;
		
		> .month-controls {
			display: flex;
			margin: 0 0 5px;
			
			> .prev,
			> .next {
				border-radius: 3px;
				flex-basis: 0;
				flex-grow: 1;
				flex-shrink: 1;
				padding: 5px;
				text-align: center;
				
				&:hover {
					background: @wtwBlue;
					color: #fff;
				}
			}
			
			> p {
				flex-basis: 0;
				flex-grow: 5;
				flex-shrink: 5;
				margin: 0;
				padding: 5px 0 0;
				text-align: center;
				.font-weight--bold();
			}
		}
		
		> .week {
			.clearfix;
			display: flex;
			margin: 0 0 5px;
			
			> label {
				flex-basis: 0;
				flex-grow: 1;
				flex-shrink: 1;
				padding: 6px;
				position: relative;
				
				& + label,
				& + .day {
					margin: 0 0 0 5px;
					font-size: 100%;
					font-weight: normal;
					text-transform: none;
				}
					
				> .day {
					background: lighten(@wtwBlue, 35%);
					height: 100%;
					left: 0px;
					padding: 6px;
					position: absolute;
					top: 0px;
					text-align: center;
					width: 100%;
					
					&:hover {
						background: @wtwBlue;
						color: #fff;
					}
				}
				
				> input[type="radio"] {
					position: absolute;
				  clip: rect(0,0,0,0);
				  pointer-events: none;
				  
				  &:focus ~ .day {
				  	.tab-focus();
				  }
				  
				  &:checked ~ .day {
				  	background: @wtwMagenta;
				  	color: #fff;
				  	
				  	&:hover {
				  		background: @wtwMagenta;
				  		color: #fff;
				  	}
				  }
				}
			}
			
			> .day {
				flex-basis: 0;
				flex-grow: 1;
				flex-shrink: 1;
				padding: 6px;
				text-align: center;
				
				& + .day,
				& + label {
					margin: 0 0 0 5px;
					font-size: 100%;
					font-weight: normal;
					text-transform: none;
				}
				
				&.sat,
				&.sun,
				&.off {
					background: @gray-light;
					color: @gray-dark;
				}
				
				&.out {
					background: @gray-lighter;
					color: @gray-darker;
				}
			}
			
			&.legend {
				
				> .day {
					background: transparent;
					padding: 0 6px;
				}
			}
		}
	}
	
	> .appt-times {
		float: left;
		height: 275px;
		margin: 0 0 0 15px;
		position: relative;
		width: 30%;
		
		> label {
			bottom: 0px;
			display: block;
			margin: 0;
			position: absolute;
			width: 100%;
			
			> select {
				display: block;
				font-size: @font-size-base;
				height: 205px;
				margin-top: 7px;
				width: 100%;
				
				> option {
					padding: 3px;
					
					&:hover {
						background: @wtwBlue;
						color: #fff;
					}
					
					&:checked {
						background-color: @wtwMagenta;
						background-image: linear-gradient(@wtwMagenta, @wtwMagenta);
						color: #fff;
						
						&:hover {
							background-color: @wtwMagenta;
							background-image: linear-gradient(@wtwMagenta, @wtwMagenta);
							color: #fff;
						}
					}
				}
			}
		}
	}
	
	&.date-only {
		width: 300px;
		
		> .appt-calendar {
			width: 100%;
		}
	}
}

