//
// Icons for BDA
//
// Since icons are fonts, they can be placed anywhere text is placed and are
// thus automatically sized to match the surrounding child. To use, create an
// inline element with the appropriate classes, like so:
//
// <a href="#"><span class="icon star"></span> Star</a>

// Import the fonts
@font-face {
  font-family: '@{indv-mkt-icons-font-name}';
  src: url('@{icon-font-path}@{indv-mkt-icons-font-name}.eot');
  src: url('@{icon-font-path}@{indv-mkt-icons-font-name}.eot') format('embedded-opentype'),
       url('@{icon-font-path}@{indv-mkt-icons-font-name}.woff') format('woff'),
       url('@{icon-font-path}@{indv-mkt-icons-font-name}.ttf') format('truetype'),
       url('@{icon-font-path}@{indv-mkt-icons-font-name}.svg') format('svg');
}

